import { Component, OnInit } from '@angular/core';
import { Properties } from '../../../properties/properties';
import {Platform} from '@ionic/angular';
import {AssemblyHouse} from '../../../interfaces/model';
import {RestService} from '../../../service/rest.service';
import {CallNumber} from "@ionic-native/call-number/ngx";

@Component({
    selector: 'app-assemblyhouses',
    templateUrl: './assemblyhouses.page.html',
    styleUrls: ['./assemblyhouses.page.scss'],
})

export class AssemblyhousesPage implements OnInit {
    housesList: AssemblyHouse [] = [];

    constructor(public properties: Properties, private platform: Platform, private rest: RestService, private callNumber: CallNumber,) { }

    ngOnInit() {
        this.rest.getAssemblyHouses().subscribe(response => {
            this.housesList = response;
        });
    }

    openmap(dest: string) {
        if (this.platform.is('ios')) {
            window.open('maps://?q=' + dest, '_system');
        } else {
            window.open('geo:0,0?q=' + dest, '_system');
        }
    }

    goCallNumber(phoneNumber: string) {
        this.callNumber.callNumber(phoneNumber, false)
            .then(res => console.log('llamando!', res))
            .catch(err => console.log('Error de llamada', err));
    }

}
